import { useSelector } from "react-redux";
import { State } from "../../redux/types";
import { formatDate, setStartDay } from "../../utils/lingvo";
import { InfographicsProps } from "../../utils/types";
import {
  InfographicsContainer,
  Label,
  Procent,
  RealData,
  RealGradient,
  RealTime,
  Calories,
  Target,
  Nutrients,
  TargetN,
  RealDataN,
  RealGradientN,
  RealTimeN,
  ProcentN,
} from "./Infographics.styles";

function Infographics({
  summCalories,
  summProtein,
  summFat,
  summCarb,
  date,
}: InfographicsProps) {
  const personData = useSelector((state: State) => state.profile);
  const targets = useSelector((state: State) => state.history);

  const targetProtein = (personData.targetMetabolism * 0.3) / 4;
  const targetFat = (personData.targetMetabolism * 0.3) / 9;
  const targetCarb = (personData.targetMetabolism * 0.4) / 4;

  const now = new Date();
  const nowDateStr = formatDate(Date.now());

  const timeDayRatio = (now.getHours() * 60 + now.getMinutes()) / (24 * 60);
  const ratio = (summ: number, target: number) => summ / target;

  const historyTarget = targets
    .slice()
    .reverse()
    .find((target) => setStartDay(target.timestamp) <= date);

  if (!Number.isFinite(ratio(summCalories, personData.targetMetabolism))) {
    return null;
  }

  if (!historyTarget) {
    return null;
  }

  return (
    <InfographicsContainer>
      {nowDateStr === formatDate(date) ? (
        <>
          <Calories>
            <Target>
              <RealTime timeDayRatio={timeDayRatio}></RealTime>
              <RealData
                calRatio={ratio(summCalories, personData.targetMetabolism)}
              >
                <RealGradient
                  calRatio={ratio(summCalories, personData.targetMetabolism)}
                ></RealGradient>
              </RealData>
              <Procent
                calRatio={ratio(summCalories, personData.targetMetabolism)}
              >
                {Math.round(
                  ratio(summCalories, personData.targetMetabolism) * 100
                )}
                %
              </Procent>
            </Target>
            <Label>Калории</Label>
          </Calories>

          <Nutrients>
            <TargetN>
              <RealTimeN timeDayRatio={timeDayRatio}></RealTimeN>
              <RealDataN calRatio={ratio(summProtein, targetProtein)}>
                <RealGradientN
                  calRatio={ratio(summProtein, targetProtein)}
                ></RealGradientN>
              </RealDataN>
              <ProcentN calRatio={ratio(summProtein, targetProtein)}>
                {Math.round(ratio(summProtein, targetProtein) * 100)}%
              </ProcentN>
            </TargetN>
            <div></div>
            <div></div>
            <Label>Белки</Label>
          </Nutrients>
          <Nutrients>
            <div></div>
            <TargetN>
              <RealTimeN timeDayRatio={timeDayRatio}></RealTimeN>
              <RealDataN calRatio={ratio(summFat, targetFat)}>
                <RealGradientN
                  calRatio={ratio(summFat, targetFat)}
                ></RealGradientN>
              </RealDataN>
              <ProcentN calRatio={ratio(summFat, targetFat)}>
                {Math.round(ratio(summFat, targetFat) * 100)}%
              </ProcentN>
            </TargetN>
            <div></div>
            <Label>Жиры</Label>
          </Nutrients>
          <Nutrients>
            <div></div>
            <div></div>
            <TargetN>
              <RealTimeN timeDayRatio={timeDayRatio}></RealTimeN>
              <RealDataN calRatio={ratio(summCarb, targetCarb)}>
                <RealGradientN
                  calRatio={ratio(summCarb, targetCarb)}
                ></RealGradientN>
              </RealDataN>
              <ProcentN calRatio={ratio(summCarb, targetCarb)}>
                {Math.round(ratio(summCarb, targetCarb) * 100)}%
              </ProcentN>
            </TargetN>
            <Label>Угл-ды</Label>
          </Nutrients>
        </>
      ) : (
        <>
          <Calories>
            <Target>
              <RealData
                calRatio={ratio(summCalories, historyTarget.targetMetabolism)}
              >
                <RealGradient
                  calRatio={ratio(summCalories, historyTarget.targetMetabolism)}
                ></RealGradient>
              </RealData>
            </Target>
            <Label>Калории</Label>
          </Calories>

          <Nutrients>
            <TargetN>
              <RealDataN calRatio={ratio(summProtein, targetProtein)}>
                <RealGradientN
                  calRatio={ratio(summProtein, targetProtein)}
                ></RealGradientN>
              </RealDataN>
            </TargetN>
            <div></div>
            <div></div>
            <Label>Белки</Label>
          </Nutrients>
          <Nutrients>
            <div></div>
            <TargetN>
              <RealDataN calRatio={ratio(summFat, targetFat)}>
                <RealGradientN
                  calRatio={ratio(summFat, targetFat)}
                ></RealGradientN>
              </RealDataN>
            </TargetN>
            <div></div>
            <Label>Жиры</Label>
          </Nutrients>
          <Nutrients>
            <div></div>
            <div></div>
            <TargetN>
              <RealDataN calRatio={ratio(summCarb, targetCarb)}>
                <RealGradientN
                  calRatio={ratio(summCarb, targetCarb)}
                ></RealGradientN>
              </RealDataN>
            </TargetN>
            <Label>Угл-ды</Label>
          </Nutrients>
        </>
      )}
    </InfographicsContainer>
  );
}

export default Infographics;
