import { useState } from "react";
import { useSelector } from "react-redux";
import { State } from "../../redux/types";
import { formatDate, setStartDay } from "../../utils/lingvo";
import {
  Day,
  DaysContainer,
  DayTitle,
  MealTitles,
  OpenDataButton,
  SubTitle,
} from "./Days.styles";
import Meals from "./Meals";

function Days() {
  const [openData, setOpenData] = useState(false);

  const meals = useSelector((state: State) => state.meals);
  const dates = Array.from(
    new Set(meals.map((meal) => setStartDay(meal.timestamp)))
  );

  return (
    <DaysContainer>
      {dates.map((date, i) => (
        <Day key={i}>
          <DayTitle>{formatDate(date)}</DayTitle>
          <OpenDataButton type="button" onClick={() => setOpenData(!openData)}>
            {openData ? "⇑ Свернуть" : "⇓ Подробнее"}
          </OpenDataButton>
          {openData ? (
            <>
              <MealTitles>
                <SubTitle>{""}</SubTitle>
                <SubTitle>б</SubTitle>
                <SubTitle>ж</SubTitle>
                <SubTitle>у</SubTitle>
                <SubTitle>к</SubTitle>
              </MealTitles>
            </>
          ) : null}

          <Meals date={date} openData={openData} />
        </Day>
      ))}
    </DaysContainer>
  );
}

export default Days;
